<template>
    <div class="page-demo">
        <!--banner-->
        <div class="banner">
            <div class="container">
                <h2 class="timeline">所有游戏</h2>
            </div>
        </div>
        <!--banner-->

        <!--游戏分类 start-->
        <div class="container" style="padding:2em 0;">
            <div class="games tj">
            </div>
            <div class="sap_tabs">

                <div id="horizontalTab" style="display: block; width: 100%; margin: 0px;">
                    <ul class="resp-tabs-list">
                        <li class="text">语言：</li><li :class="activeClass == 0 ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(0)"><span>全部</span></li><li :class="activeClass == 1 ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(1)"><span>已汉化</span></li><li :class="activeClass == 2 ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(2)"><span>汉化中</span></li><li :class="activeClass == 14 ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(14)"><span>英文</span></li>
                        <div class="clearfix"></div>
                    </ul>
                    <ul class="resp-tabs-list">
                        <li class="text">平台：</li><li :class="activeClass == 0 ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(0)"><span>全部</span></li><li :class="activeClass == 13 ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(13)"><span>K站</span></li><li :class="activeClass == 18 ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(18)"><span>PC</span></li><li :class="activeClass == 19 ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(19)"><span>Steam</span></li><li :class="activeClass == 16 ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(16)"><span>安卓</span></li><li :class="activeClass == 17 ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(17)"><span>苹果</span></li>
                        <div class="clearfix"></div>
                    </ul>
                    <ul class="resp-tabs-list">
                        <li class="text">类型：</li><li :class="activeClass == 0 ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(0)"><span>全部</span></li><li v-for="(t,index) in typeList" :class="activeClass == index ? 'resp-tab-item resp-tab-active':'resp-tab-item'" @click="getItem(index)" v-if="index != 0 && index != 1 && index != 2 && index != 14 && index != 13 && index != 16 && index != 17 && index != 18 && index != 19"><span>{{t}}</span></li>
                        <div class="clearfix"></div>
                    </ul>
                    <div class="resp-tabs-container">
                        <div v-for="(t,index) in typeList" class="tab-1 resp-tab-content'" :aria-labelledby="'tab_item-'+index" v-if="activeClass==index">
                            <section>
                                <ul id="da-thumbs" class="da-thumbs">
                                    <li v-for="game in gameData">
                                        <!--非Flash-->
                                        <em class="b-link-stripe b-animate-go  thickbox" v-if="!game.flash">
                                            <router-link :to="'/game/'+game.path" :title="game.title" target="_blank">
                                                <img :src="'static/images/game/'+game.path+'/face.jpg'" alt="" />
                                            </router-link>
                                            <div>
                                                <h5>{{game.title}} <span v-if="game.version">v {{game.version}}</span></h5>
                                                <span></span>
                                                <p>
                                                    <router-link class="hvr-wobble-top" :to="'/game/'+game.path" :title="game.title" target="_blank">
                                                        看介绍
                                                    </router-link>
                                                    <a class=" hvr-wobble-top" :href="game.zhUrl?game.zhUrl:game.enUrl" target="_blank">玩游戏</a>
                                                </p>
                                            </div>
                                        </em>
                                        <!--Flash-->
                                        <em class="b-link-stripe b-animate-go  thickbox" v-if="game.flash">
                                            <a :href="'static/flash/'+game.path+'.html'"  :title="game.title" target="_blank">
                                                <img :src="'static/images/game/'+game.path+'/face.jpg'" alt="" />
                                            </a>
                                            <div>
                                                <h5>{{game.title}}</h5>
                                                <span></span>
                                                <p>
                                                    <a class=" hvr-wobble-top" :href="'static/flash/'+game.path+'.html'"  :title="game.title" target="_blank">
                                                        看介绍
                                                    </a>
                                                    <a class=" hvr-wobble-top" :href="'static/flash/'+game.path+'.html'"  :title="game.title" target="_blank">
                                                        玩游戏
                                                    </a>
                                                </p>
                                            </div>
                                        </em>
                                    </li>

                                    <div class="clearfix"> </div>
                                </ul>
                            </section>
                        </div>
                    </div>
                    <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[15, 30, 60, 90]" :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
                    </el-pagination>
                </div>
            </div>
        </div>
        <!--游戏分类 end-->
    </div>
</template>

<script>
    export default {
        name: 'Demo',
        components: {},
        data() {
            return {
                total: 0,
                currentPage: 1, //默认显示第1页
                pagesize: 15, //默认一次显示10条数据
                typeList: [],
                gameList: [],
                curType: '全部游戏',
                activeClass: 0
            }
        },
        created() {
            this.getType();
            this.getData(0);
            //SyntaxHighlighter.all();

        },
        mounted() {

        },
        computed: {
            gameData: function() {
                return this.gameList.slice((this.currentPage - 1) * this.pagesize, this.currentPage * this.pagesize);
            }
        },
        methods: {
            getType() {
                this.$http.get('static/data/type.json').then(res => {
                        this.typeList = res.data[0];
                        this.curType = this.typeList[this.activeClass]
                    },
                    res => {
                        console.log("error");
                    }
                )
            },
            getData() {
                this.$http.get('static/data/games.json').then(game => {

                    if (this.curType == "全部游戏") {
                    for (var i = 0; i < game.data.length; i++) {
                        if(game.data[i].status==1) {
                            this.gameList.push(game.data[i]);
                        }
                    }
                    } else {
                        for (var i = 0; i < game.data.length; i++) {
                            if(game.data[i].status==1){
                            game.data[i].type.forEach(item => {
                                // console.log(this.curType)
                                if(item == this.curType){
                                this.gameList.push(game.data[i]);
                            }
                        });
                        }
                    }
                }

                this.total = this.gameList.length;
                })
            },
            getItem(index) {
                this.activeClass = index;
                this.gameList = [];
                //获取点击的分类
                this.$http.get('static/data/type.json').then(res => {
                    this.curType = this.typeList[index]
                })
                this.getData();
                //显示第一页数据
                this.currentPage=1
            },
            handleSizeChange(size) {
                this.pagesize = size;
            },
            handleCurrentChange(currentPage) {
                this.currentPage = currentPage;
            }
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



