<template>
    <div class="page-blog">
        <!--banner-->
        <div class="banner">
            <div class="container">
                <h2 class="timeline">搜索结果</h2>
            </div>
        </div>
        <!--banner-->

        <div class="blog">
            <div class="container" v-if="isShowTip==1">
                <div class="blog-head">
                    <div class="col-md-4 blog-top" v-for="g in gameData" v-if="pagesize && g.status!=0">
                        <div class="blog-in" v-if="!g.flash">
                            <router-link  :to="'/game/'+g.path" :title="g.title" target="_blank">
                                <img class="img-responsive" :src="'static/images/game/'+g.path+'/face.jpg'" :alt="g.title">
                            </router-link>
                            <div class="blog-grid">
                                <h4><router-link  :to="'/game/'+g.path" :title="g.title" target="_blank">{{g.title}} <span v-if="g.enTitle">（{{g.enTitle}}）</span><span v-if="g.version">v {{g.version}}</span></router-link></h4>
                                <p></p>

                                <div class="date">
                                    <span class="date-in" title="发布日期"><i class="glyphicon glyphicon-calendar"> </i>{{g.date}}</span>
                                    <span class="date-in" title="最后更新日期" v-if="g.update"><i class="glyphicon glyphicon-refresh"> </i>{{g.update}}</span>
                                    <div class="clearfix"> </div>
                                </div>
                                <div class="more-top">
                                    <router-link class="hvr-wobble-top" :to="'/game/'+g.path" :title="g.title" target="_blank">
                                        查看详情
                                    </router-link>
                                </div>
                            </div>
                        </div>
                        <!--Flash-->
                        <div class="blog-in" v-if="g.flash">
                            <a :href="'static/flash/'+g.path+'.html'" :title="g.title" target="_blank">
                                <img class="img-responsive" :src="'static/images/game/'+g.path+'/face.jpg'" :alt="g.title">
                            </a>
                            <div class="blog-grid">
                                <h4><a :href="'static/flash/'+g.path+'.html'" target="_blank">{{g.title}}<span v-if="g.enTitle">（{{g.enTitle}}）</span></a></h4>
                                <p></p>

                                <div class="date">
                                    <span class="date-in"><i class="glyphicon glyphicon-calendar"> </i>{{g.date}}</span>
                                    <div class="clearfix"> </div>
                                </div>
                                <div class="more-top">
                                    <a class=" hvr-wobble-top" :href="'static/flash/'+g.path+'.html'" :title="g.title" target="_blank">查看详情</a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="clearfix"> </div>
                </div>
                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[15, 30, 60, 90]" :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
            <div class="nodata" v-if="isShowTip == 0">
                没有搜到关于 “<span>{{keyword}}</span>” 游戏，您可以前往 “<span>游戏</span>” 菜单按分类筛选游戏
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Demo',
        components: {},
        data() {
            return {
                keyword:this.$route.params.id,
                gameList: [],
                resultList: [],
                isShowTip:0,
                total: 0,
                currentPage: 1, //默认显示第1页
                pagesize: 15 //默认一次显示10条数据
            }
        },
        created() {
            this.resultList = []
            this.getData()
        },
        mounted() {},
        computed: {
            gameData: function() {
                return this.resultList.slice((this.currentPage - 1) * this.pagesize, this.currentPage * this.pagesize);
            }
        },
        watch: {
            $route: {
                handler: function(val, oldVal){
                    this.keyword=this.$route.params.id;
                    this.getData()
                }
            }
        },
        methods: {
            getData() {
                this.resultList = []
                this.$http.get('static/data/games.json').then(game => {
                    for(var i=0;i<game.data.length;i++){
                        if(game.data[i].status==1){
                            // console.log(game.data[i].title.indexOf(this.keyword))
                            if (game.data[i].title.indexOf(this.keyword) > -1 || game.data[i].path.indexOf(this.keyword) > -1 || game.data[i].enTitle.indexOf(this.keyword) > -1) {

                                this.resultList.push(game.data[i])
                            }
                            // this.gameList.push(game.data[i]);
                        }
                    }

                this.total=this.resultList.length


                if (this.resultList.length == 0) {   //如果没有匹配结果，就显示提示信息
                    this.isShowTip = 0
                }else{
                    this.isShowTip = 1
                }
                })
            },
            handleSizeChange(size) {
                this.pagesize = size;
            },
            handleCurrentChange(currentPage) {
                this.currentPage = currentPage;
            }
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



